<template>
  <div class="wrapper" ref="wrapper">
    <div class="content">
      <slot></slot>
    </div>
  </div>
</template>

<script>
// wrapperoverflow: hidden

import BScroll from "better-scroll";
export default {
  name: "scroll",
  //  probeTypepullUpLoad positionpullUpLoad
  props: {
    probeType: {
      type: Number,
      default: 1
    },
    pullUpLoad: {
      type: Boolean,
      default: false
    }
  },

  data() {
    return { scroll: null, aa: 12 };
  },
  mounted() {
    this.scroll = new BScroll(this.$refs.wrapper, {
      probeType: this.probeType,
      click: true,
      pullUpLoad: this.pullUpLoad
    });

    //  ()
    this.scroll.on("scroll", position => {
      // console.log(position.y);
      this.$emit("scroll", position); //position
    });

    //  finishPullUp()
    this.scroll.on("pullingUp", () => {
      // console.log("pullingUp");
      this.$emit("pullingUp");

      setTimeout(() => {
        this.scroll.finishPullUp();
      }, 5000);
    });

    // this.scroll
    // 1: main.js, $bus, Vue.prototype.$bus = new Vue(); : vue
    //                 $bus, $busvue;
    //                 this.$bus.$emit('', ); ,
    //                 this.$bus.$on('',()=>{   });

    //          2: divdivimg

    this.scroll.refresh();
  },
  methods: {
    scrollTo(x, y, time = 500) {
      // new BScrollscrollTo
      // console.log(y);

      this.scroll && this.scroll.scrollTo(x, y, time);
    },

    finishPullUp() {
      this.scroll.finishPullUp();
    },

    refresh() {
      this.scroll && this.scroll.refresh();
      console.log("refresh");
    }
  }
};
</script>

<style scoped>
