F<template>
  <div class="myorder">
    <Mytitle v-slot></Mytitle>
    <van-tabs v-model="active" class="myorder-box" @click="onClick" :swipe-threshold='5' color='rgb(86, 150, 247)' line-width='20%' line-height='2px' title-active-color='rgb(86, 150, 247)'>
      <van-tab :title="item" v-for="item in navs" :key='item'>
        <!--     /   -->
        <div v-if='havelist'>
          <!--  -->
          <div class="dingdanbox" v-for="item in list" :key='item._id'>
            <!--  -->
            <van-row class="paytime">
              {{item.time}}
            </van-row>
            <!--  -->
            <div class="paycontentbox">
              <van-row class="paycontent">
                <van-col span='10' class="paycontent-img">
                  <img :src="item.src" alt="">
                </van-col>
                <van-col span='14' class="paycontent-title">
                  <p>{{item.title}}</p>
                  <div>2080/</div>
                </van-col>
              </van-row>
            </div>

            <!--  -->
            <van-row class="payprice">
              <van-col span='12'>
                <span class="price-span">{{item.price}}</span>
              </van-col>
              <van-col span='12' class="payprice-right">
                
                <span class="price-span">{{item.pay}}</span>
              </van-col>
            </van-row>
            <!--  -->
            <van-row class="paystatus">
              <van-col span='12'>{{item.status}}</van-col>
              <!--  -->
              <van-col span='12' class="paystatus-right">
                <div v-if='item.status===""'>
                  <span class="failpay" @click="removepay(item._id)"></span>
                  <span class="successpay" @click="topay(item._id)"></span>
                </div>
                <div v-else-if='item.status===""'>
                  <span class="failpay" @click='torefund(item._id)'></span>
                  <span class="successpay"></span>
                </div>
                <div v-else-if='item.status===""'>
                  <!-- <span class="failpay"></span> -->
                  <span class="successpay"></span>
                </div>
                <div v-else-if='item.status===""'>
                  <!-- <span class="failpay"></span> -->
                  <span class="successpay"></span>
                </div>
                <div v-else-if='item.status===""'>
                  <!-- <span class="failpay"></span> -->
                  <span class="successpay"></span>
                </div>

              </van-col>
            </van-row>
          </div>
          <div class="theend">!</div>
        </div>
        <div v-else>

          <van-row type="flex" justify="center">
            <img :src="kongurl" alt="" class="nolistimg">
          </van-row>
        </div>

      </van-tab>

    </van-tabs>

  </div>
</template>
<script>
import Mytitle from "./Mytitle.vue";
export default {
  components: {
    Mytitle
  },
  data() {
    return {
      active: 0,
      navs: ["", "", "", "", ""],
      list: [],
      havelist: true,
      kongurl: require("../assets/img/kong.gif")
    };
  },

  methods: {
    activelist() {
      // 
      this.$mycart.get().then(res => {
        let { data: { data } } = res;
        this.changestatus(data);
        // console.log(data);
      });
    },
    changestatus(data) {
      if (data[0]) {
        //
        this.havelist = true;
        data.forEach(element => {
          if (element.status == "1") {
            element.status = "";
          }
          if (element.status == "2") {
            element.status = "";
          }
          if (element.status == "3") {
            element.status = "";
          }
          if (element.status == "4") {
            element.status = "";
          }
          if (element.status == "5") {
            element.status = "";
          }
        });
      } else {
        this.havelist = false;
      }
      this.list = data;
    },
  async onClick(name, title) {
      // 
      if (name == 0) {
        // 
        this.activelist();
      } else {
        // 
        if (name <= 3) {
          this.$mycart.post("/status", { status: String(name) }).then(res => {
            let { data: { data } } = res;
            this.changestatus(data);
          });
        } else {
           let result=await this.$mycart.post("/status", { status:'4' })
           let result1=await this.$mycart.post("/status", { status:'5' })
           let data= [...result.data.data,...result1.data.data]
            this.changestatus(data);
        }

        //
      }
    },
    removepay(id) {
      // 
      this.$mycart.delete(`/${id}`).then(res => {
        if (this.active == 0) {
          this.activelist();
        } else {
          // 
          this.$mycart.post("/status", { status: "1" }).then(res => {
            let { data: { data } } = res;
            this.changestatus(data);
          });
        }
      });
    },
    torefund(id) {
      //     ==
      this.$mycart({
        url: `/${id}`,
        method: "patch",
        data: {
          status: "4"
        }
      }).then(res => {
        if (res.data.msg == "success") {
          this.active = 4;
          this.onClick(4, "");
        }
      });

      // this.active=4;
    },
    topay(id) {
      //    == 
      this.$mycart({
        url: `/${id}`,
        method: "patch",
        data: {
          status: "2"
        }
      }).then(res => {
        if (res.data.msg == "success") {
          this.active = 2;
          this.onClick(2, "");
        }
      });
    }
  },
  created() {
    this.activelist();
  }
};
</script>
<style scoped>
.myorder-box {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  background: #f3f3f3;
  top: 52px;
}
.dingdanbox {
  background: #ffffff;
  margin: 10px 0;
}

.paycontentbox {
  padding: 0 10px;
}
.paycontent {
  padding: 10px 0;
  border-bottom: 1px solid #f3f3f3;
}

.paycontent-img img {
  width: 100%;
  max-height: 100%;
}

.paycontent-title {
  padding-left: 15px;
}
.paycontent-title > p {
  font-size: 14px;
  margin: 0;
}
.paycontent-title > div {
  color: #cccccc;
  font-size: 12px;
  margin-top: 10px;
}
.payprice {
  height: 35px;
  line-height: 35px;
  padding: 0 10px;
  border-bottom: 1px solid #f3f3f3;
  font-size: 14px;
}
.price-span {
  color: orangered;
}

.payprice-right {
  text-align: right;
}
.paystatus {
  height: 40px;
  line-height: 40px;
  padding: 0 10px;
  color: #666666;
  font-size: 14px;
}

.failpay {
  display: inline-block;
  background: #cccccc;
  color: #ffffff;
  padding: 5px;
  border-radius: 4px;
  font-size: 12px;
  line-height: 12px;
}

.successpay {
  display: inline-block;
  background: #ee4b45;
  color: #ffffff;
  padding: 5px;
  border-radius: 4px;
  font-size: 12px;
  line-height: 12px;
  margin-left: 5px;
}

.paytime {
  height: 30px;
  font-size: 12px;
  line-height: 30px;
  padding: 0 15px;
  color: #666666;
  border-bottom: 1px solid #f3f3f3;
}

.paystatus-right {
  text-align: right;
}
.theend {
  text-align: center;
  color: #666666;
  font-size: 12px;
  /* margin: 20px 0; */
  padding: 30px 0 50px 0;
  margin-top: -10px;
  background: #f3f3f3;
}
.nolistimg {
  margin-top: 30px;
}
</style>
